@media screen and (orientation:landscape)
{
   body
   {
    background: url('../assets/Horizontal_bg.png');
   }

   .nav-area{
    height: 164px;
  }

  .nav-area .title{
    position: absolute;
    top:57px;
    line-height: 42px;
    left: 104px;
  }
  .title-pos{
    top:57px;
    float: left;
    margin-right: 241px;
  }
}

@media screen and (orientation:portrait)
{
   body
   {
    background: url('../assets/Vertical_bg.png') no-repeat;
   }
   .nav-area{
    height: 165px;
    background-color: rgba(226, 225, 224, .99);
    /*box-shadow: 0px 1px 0px rgba(0,0,0,.1);*/
  }
  .nav-area .title{
    position: absolute;
    top:42px;
    line-height: 42px;
    left: 68px;  
  }
  .info-txt{
    width: 940px;
  }
}