

.nav-area {
    position: absolute;
    top:0px;
    width: 100%;
    
    float: left;
    
    /*background: url('../assets/nav_ForYou_focus.png') no-repeat center center;*/
    background-size:100% 100%;
    z-index: 9;
}

.nav-fonts{  
  font-family: SamsungOneLatin-800;
  font-size: 48px;
  color: #4466CC;
  letter-spacing: 0;
}

.nav-area .foryou {
  position: absolute;
  top:57px;
  line-height: 42px;

  left:412px;
}
.nav-name-fonts {
  opacity: 0.36;
  font-family: SamsungOneUI-500;
  font-size: 46px;
  color: #383838;
  letter-spacing: 0;
  line-height: 50px;
}

.nav-area .focus-title-area {
  position: absolute;
  left:412px;
  height: 100%;
  width: 1400px;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.focus-title {
  position: relative;  
  width: 220px;
  height: 50px;  
  font-family: SamsungOneUI-500;
  font-size: 46px;  
  letter-spacing: 0;
  line-height: 50px;

  opacity: 0.36;
  color: #383838;
}

.focus-title-focused {
  position: relative;  
  width: 220px;
  height: 50px;  
  font-family: SamsungOneUI-500;
  font-size: 46px;  
